body {
  background-image: url("../../assets/pokemon-background.jpg");
  background-size: cover;
}
.thumb-container2 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 1.5rem 0 ;
  margin: 0.3rem;
  border: 1px solid #efefef;
  border-radius: 0.2rem;
  min-width: 30vw;
  min-height: 30vw;
  text-align: center;
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.089);
  width: 100%;
}
.detail-wrapper2 {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: flex-start;
  align-items: center;

  img {
    width: 120px;
    height: 120px;
  }

  h2 {
    font-size:xx-large;
  }

  .stats {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;
  }

  
}

.buttons {
  display: flex;
  justify-content: space-between;
}







